import React, { useState } from "react";
import { Form, Label, Input, FormGroup, Button } from "reactstrap";
import DeliveryInfoValidation from "../Validation/DeliveryInfoValidation";

function DeliveryInfo({ deliveryInfo, handleDeliveryChange, next, back }) {
	const [errors, setErrors] = useState({});

	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-4 offset-lg-4 border px-5 pb-5">
				<legend>Delivery Information</legend>
				<FormGroup className="mt-3">
					<Input
						name="street"
						type="text"
						defaultValue={deliveryInfo.street}
						onChange={handleDeliveryChange}
						placeholder="Street number"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.street ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.street}
					</div>
				</FormGroup>
				<FormGroup className="form-row">
					<Input
						name="suburb"
						type="text"
						defaultValue={deliveryInfo.suburb}
						onChange={handleDeliveryChange}
						placeholder="Suburb"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.suburb ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.suburb}
					</div>
					<Input
						name="town"
						type="text"
						defaultValue={deliveryInfo.town}
						onChange={handleDeliveryChange}
						placeholder="Town"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.town ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.town}
					</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="postCode"
						type="number"
						defaultValue={deliveryInfo.postCode}
						onChange={handleDeliveryChange}
						placeholder="Postal Code / ZIP Code"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.postCode ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.postCode}
					</div>
				</FormGroup>
				<Button
					color="primary"
					block
					onClick={() => {
						setErrors(next(deliveryInfo, DeliveryInfoValidation));
					}}
				>
					Next
				</Button>
				<Button
					color="secondary"
					block
					onClick={() => {
						back();
					}}
				>
					Back
				</Button>
			</fieldset>
		</React.Fragment>
	);
}

export default DeliveryInfo;
